import { Component, OnInit } from '@angular/core';
import { Base } from '../../../model/base';
import { ObraService } from '../../../service/sigobra/obra.service';
import { Filtro } from '../../../model/objetc/filtro/filtro';
import { Pesquisa } from '../../../model/objetc/filtro/pesquisa';
import { InspecaoService } from '../../../service/sigobra/inspecao.service';
import { Obras } from './../../../model/objetc/obras';
import { Generic } from '../../../model/objetc/filtro/generic';
import { JoinType } from '../../../model/enums/join-type.enum';


@Component({
  selector: 'app-selecao-relatorio',
  templateUrl: './selecao-relatorio.page.html',
  styleUrls: ['./selecao-relatorio.page.scss'],
})
export class SelecaoRelatorioPage implements OnInit {

  // dados para serem preenchidos nos filtros
  obras: Array<Obras>;

  opcoesDatas = [
    { index: 0, nome: 'Não filtar' },
    { index: 1, nome: 'Abertura' },
    { index: 2, nome: 'Fechamento' },
  ];

  opcoesStatus = [
    { index: 0, nome: 'Não filtar' },
    { index: 1, nome: 'Aberta' },
    { index: 2, nome: 'Fechada' },
  ];


  // dados informados nos filtros
  // dados relaciodos a obras
  statusObra: Generic = new Generic();
  dataDeApuracao: Generic = new Generic();
  dataInicial: string;
  datafinal: string;

  // dados selecionados a inspeção
  obra: Obras;
  statusDaInpecaoApuracao: Generic = new Generic();
  dataDeInpecaoApuracao: Generic = new Generic();
  dataInspecaoInicial: string;
  dataInspecaofinal: string;


  constructor(private base: Base,
    private os: ObraService,
    private is: InspecaoService) { }

  filtroObra: Filtro = new Filtro();

  ngOnInit() {
    this.filtroObra.nativo = true;
    this.filtroObra.sql = 'select obr.* from obras obr left join inspecao ins on ins.obras_id = obr.id where obr.id > 0 ';
  }

  ionViewWillEnter() {
    this.carregarObras(" ");
  }

  carregarObras(value: string) {
    this.base.present();
    return this.os.pesquisar(value).subscribe(
      data => {
        this.obras = data as Array<Obras>;
        this.base.dismiss();
      }, error => {
        this.base.dismiss();
        this.base.mensagemErro('Falha  ao listar  Obras ' + this.base.tratarErro(error));
      }
    );
  }

  // relatorio de obras  - levar em consideraçã a data da obra
  getRelatorioObras() {
    this.filtroObra.nativo = true;
    this.filtroObra.sql = 'select obr.* from obras obr where obr.id > 0 ';
    this.filtroObra.pesquisas = new Array<Pesquisa>();
    // tslint:disable-next-line: ban-types
    let campo: string = this.dataDeApuracao.index !== 0 ? this.dataDeApuracao.index === 1 ? ' obr.dtinicio ' : ' obr.dtfinalizada ' : '';

    if (campo !== '') {
      const retorno = this.getDatas(' and  ' + campo, this.dataInicial, this.datafinal);
      this.filtroObra.pesquisas = this.filtroObra.pesquisas.concat(retorno);
    }

    campo = this.dataDeApuracao.index !==
      0 ? this.dataDeApuracao.index === 1 ? '  and obr.dtfinalizada is  null ' : ' and obr.dtfinalizada is not null ' : '';

    let pesquisa: Pesquisa = new Pesquisa();
    pesquisa.comparacao = '   ';
    pesquisa.valor = '  ';
    pesquisa.nome = 'and obr.ativo is true';
    this.filtroObra.pesquisas.push(pesquisa);
    if (campo !== '') {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = ' ';
      pesquisa.valor = ' ';
      pesquisa.nome = campo;
      this.filtroObra.pesquisas.push(pesquisa);
    }

    this.base.present();
    return this.os.getRelatorioFiltro(this.filtroObra).subscribe(
      data => {
        this.base.dismiss();
        this.base.abrirPDF(data, 'declaração');
      }, error => {
        this.base.dismiss();
        console.log(error);
        this.base.mensagemErro('Erro ao gerar relatório '
          + this.base.tratarErro(error));
      }
    );
  }

  //
  getRelatorioInspecaoPorObra(consolidado: boolean = false) {
    if (this.obra === undefined || this.obra === null || this.obra.id === undefined || this.obra.id === null || !(this.obra.id > 0)) {
      this.base.mensagemErro('E necessário selecionar uma obra');
      return;
    }
    const filtroInspecao: Filtro = new Filtro();
    filtroInspecao.pesquisas = new Array<Pesquisa>();

    const campo: string =
      this.dataDeInpecaoApuracao.index !== 0 ? this.dataDeInpecaoApuracao.index === 1 ? 'dtinicio' : 'dtfinalizada' : '';

    if (campo !== '') {
      filtroInspecao.pesquisas = filtroInspecao.pesquisas.concat(this.getDatas(campo, this.dataInspecaoInicial, this.dataInspecaofinal));
    }

    let pesquisa = new Pesquisa();
    pesquisa.comparacao = '=';
    pesquisa.nome = 'id';
    pesquisa.tabela = 'obras';
    pesquisa.tipe = JoinType.INNER;
    pesquisa.valor = this.obra.id.toString();
    filtroInspecao.pesquisas.push(pesquisa);

    pesquisa = new Pesquisa();
    pesquisa.comparacao = '=';
    pesquisa.nome = 'ativo';
    pesquisa.tabela = 'obras';
    pesquisa.tipe = JoinType.INNER;
    pesquisa.valor = 'true';
    filtroInspecao.pesquisas.push(pesquisa);


    if (this.statusDaInpecaoApuracao.index === 1) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = 'is null';
      pesquisa.nome = 'dtfinalizada';
      filtroInspecao.pesquisas.push(pesquisa);
    } else {
      if (this.statusDaInpecaoApuracao.index === 2) {
        pesquisa = new Pesquisa();
        pesquisa.comparacao = 'is not null';
        pesquisa.nome = 'dtfinalizada';
        filtroInspecao.pesquisas.push(pesquisa);
      }

    }

    this.base.present();
    if (consolidado) {
      this.is.getRelatorioFiltroObraConsolidado(filtroInspecao).subscribe(
        data => {
          this.base.dismiss();
          this.base.abrirPDF(data, 'declaração');
        }, error => {
          this.base.dismiss();
          console.log(error);
          this.base.mensagemErro('Erro ao gerar relatório ' + this.obra.nome
            + this.base.tratarErro(error));
        }
      );
    } else {
      this.is.getRelatorioFiltroObra(filtroInspecao).subscribe(
        data => {
          this.base.dismiss();
          this.base.abrirPDF(data, 'declaração');
        }, error => {
          this.base.dismiss();
          console.log(error);
          this.base.mensagemErro('Erro ao gerar relatório ' + this.obra.nome + ' '
            + this.base.tratarErro(error));
        }
      );
    }
  }


  //  aqui e relatorio de inspeção aquiu levar em consideração as inspeços
  getRelatorioDeInspecao() {
    const filtroInspecao: Filtro = new Filtro();
    filtroInspecao.pesquisas = new Array<Pesquisa>();
    // tslint:disable-next-line: ban-types
    const campo: string =
      (this.dataDeInpecaoApuracao.index !== 0) ? this.dataDeInpecaoApuracao.index === 1 ? 'dtinicio' : 'dtfinalizada' : '';

    if (campo !== '') {
      filtroInspecao.pesquisas = filtroInspecao.pesquisas.concat(this.getDatas(campo, this.dataInspecaoInicial, this.dataInspecaofinal));
    }

    let pesquisa = new Pesquisa();
    pesquisa = new Pesquisa();
    pesquisa.comparacao = '=';
    pesquisa.nome = 'ativo';
    pesquisa.valor = 'true';
    filtroInspecao.pesquisas.push(pesquisa);

    if (this.statusDaInpecaoApuracao.index === 1) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = 'is null';
      pesquisa.nome = 'dtfinalizada';
      filtroInspecao.pesquisas.push(pesquisa);
    } else {
      if (this.statusDaInpecaoApuracao.index === 2) {
        pesquisa = new Pesquisa();
        pesquisa.comparacao = 'is not null';
        pesquisa.nome = 'dtfinalizada';
        filtroInspecao.pesquisas.push(pesquisa);
      }

    }

    this.base.present();
    return this.is.getRelatorioFiltro(filtroInspecao).subscribe(
      data => {
        this.base.dismiss();
        this.base.abrirPDF(data, 'declaração');
      }, error => {
        this.base.dismiss();
        console.log(error);
        this.base.mensagemErro('Erro ao gerar relatório ' + this.obra.nome + ' '
          + this.base.tratarErro(error));
      }
    );
  }


  getDatas(campo: string, dataInicia: string, dataFinal: string): Array<Pesquisa> {
    const pesquisas = new Array<Pesquisa>();
    let pesquisa: Pesquisa = new Pesquisa();
    if (!this.base.vazio(dataInicia)) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '>=';
      pesquisa.valor = '\'' + dataInicia + '\'';
      pesquisa.nome = campo;
      pesquisas.push(pesquisa);
    }

    if (!this.base.vazio(dataFinal)) {
      pesquisa = new Pesquisa();
      pesquisa.comparacao = '<=';
      pesquisa.valor = '\'' + dataFinal + '\'';
      pesquisa.nome = campo;
      pesquisas.push(pesquisa);
    }
    return pesquisas;
  }


}
